import React, {Component} from 'react';
import PropTypes from 'prop-types';
import DataItem from './DataItem';
import '../constants/Utils.js';

var table_style ={
    "backgroundColor" : "#d0e4fe",
    "border" : "1",
    "width" : "100%", 
    "cellspacing" : "1", 
    "cellpadding" : "0",
    "color" : "orange",

}
class DisplayList extends Component{

    componentDidMount() {
        console.log("DisplayList---componentDidMount---");
        
    }
    shouldComponentUpdate(NEXTPROPS , NEXTSTATE ){
        return true;
    }

    static get defaultProps(){
        return {
            data_arrays : [],
            sendRedPacketListener: (wallertAddress, money)=>{},
        }
    }
    static propsTypes={
        data_arrays: PropTypes.arrayOf(PropTypes.object).isRequired,
        sendRedPacketListener: PropTypes.func.isRequired,
    }
    render() {
        const {
            data_arrays,
            sendRedPacketListener,
        }= this.props;

        console.log("==> arrays data =  "+data_arrays);
        var num = 0;
        return (
            <table id="table_all_data_id" className="altrowstable css_hor_center" style={{margin: 20}} >
            <tbody>
                <tr>
                    <th>序号</th>
                    <th>头像</th>
                    <th>姓名</th>
                    <th>性别</th>
                    <th>关系</th>
                    <th>生日/纪念日</th>
                    <th>电话</th>
                </tr>
                {
                    data_arrays.map((itemObj)=>{
                        console.log("loop in ---itemObj = ",itemObj);
                        if(itemObj === null) {
                            return;
                        }
                        num++;
                        return (
                            <DataItem key={num}
                                aid={num}  
                                dataItem={itemObj}
                                sendRedPacketListener = {this.props.sendRedPacketListener} />
                            );
                    })
                }
                </tbody>
             </table>
      
        )
    }
}
export default DisplayList;


