import React, { Component } from 'react';
import PropTypes from 'prop-types';
import '../res/css/main.css';
import '../res/css/animate.min.css';
import '../res/css/bootstrap.min.css';
import '../res/css/lightbox.css';
import '../res/css/main.css';
import '../res/css/responsive.css';
import '../res/css/form.css';
import '../App.css';
import '../res/css/home.css';
import DisplayList from './DisplayList';
import Utils from '../constants/Utils.js';
import Footer from './Footer.js';


class Home extends Component{

    static get defaultProps() {
        return {
            listDataArrays: [],
            submitAddListener: (strJsonData) => {},
            queryDataListener: (wallet_address)=>{},
            sendRedPacketListener: (wallet_address, money)=>{},
        }
    }

    static propTypes(){
        listDataArrays: PropTypes.arrayOf(PropTypes.object).isRequired;
        submitAddListener: PropTypes.func.isRequired;
        queryDataListener: PropTypes.func.isRequired;
        sendRedPacketListener: PropTypes.func.isRequired;
    }
    topLayer() {
        return (
        <div id="#top_layer">
            <div class="collapse navbar-collapse" style={{background: "#ffffff", paddingRight: 100}}>
                <ul class="nav navbar-nav navbar-right" style={{background: "#ffffff"}}>
                    <li class="active" style={{background: "#ffffff"}}><a href="home-slider">返回首页</a></li>
                    <li class="active" style={{background: "#ffffff"}}><a href="https://blog.nebulas.io/2018/04/12/creating-a-nas-wallet/" target="_blank" class="learn-btn animated fadeInUp">使用帮助 <i class="fa fa-arrow-down"></i></a></li>    
                </ul>
            </div>
            <section id="error-page">
                <div class="error-page-inner">
                    <div class="container-fluid">
                        <div class="row">
                            <div class="col-sm-12">
                                <div class="text-center">
                                    <div class="bg-404">
                                        <div class="error-image">                                
                                            <img className="../res/css/img-responsive" src={require("../res/images/365days.png")} alt=""/>  
                                        </div>
                                    </div>
                                    <h2>星云.生日管家</h2>
                                    <a href="#" className="btn  btn-submit">星云.生日管家.   Sweet every day.  星云.生日管家. Sweet every day. 星云.生日管家. Sweet every day.</a>
                                    
                                    <div class="form-group">
                                    <input class="css_input css_search_input" type="text" name="address" ref="address" 
                                        required="required" autoFocus={true} placeholder="查询请输入您的钱包地址[以n1开头]"/>
                                    </div>
                                    <div>如果还没有钱包地址请<a href="https://blog.nebulas.io/2018/04/12/creating-a-nas-wallet/" target="_blank">参见帮助</a>，申请您的钱包地址。</div>
                                    
                                    <div className="css_inputbox_div">
                                        <input type="submit" name="query" class=" css_home_button css_home_button_search" value="查询 （以您的钱包地址作为key查询）" 
                                        onClick={()=>{
                                            var walletAddress = this.refs.address.value;
                                            console.log("click display button inputbox value= "+walletAddress);
                                            
                                            this.props.queryDataListener(walletAddress);
                                            window.location = window.location.href.split('#')[0]+'#addItems';
                                            
                                        }}/>
                                      
                                        <input type="submit" name="add" class=" css_home_button css_home_button_add" value="添加" 
                                        onClick={()=>{
                                            console.log("click add button");
                                            window.location = window.location.href.split('#')[0]+'#addItems';
                                        }}/>
                                    </div>
                                    <div>点击添加前请确认已安装<a href="https://github.com/nebulasio/WebExtensionWallet" target="_blank">Nas钱包插件</a></div>

                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            </div>
            )
    }
    inputForm() {
        return(
            <fieldset>
                {/* <legend >添加生日</legend> */}
                <table className="css_div_table" style={{margin: 20, marginBottom: 80}}>
                <tbody>
                    <tr>
                    <td className="css_div_table_td css_home_text">分类：</td>
                    <td colSpan='2'>
                    <label htmlFor="r1" className="css_home_text"><input id="c1" style={{margin: 10}} type="radio" value="0" name="category" defaultChecked/> 生日</label>  
                    <label htmlFor="r2" style={{margin: 20}} className="css_home_text" ><input style={{margin: 10}} id="c2" type="radio" value="1" name="category"/>纪念日</label>  
                    </td>
                    </tr>

                    <tr>
                    <td className="css_div_table_td css_home_text" >姓名：</td>
                    <td><input className="css_input" ref="name" id={100} type="text" placeholder="请输入姓名" /></td>
                
                    </tr>
                    <tr>
                    <td className="css_div_table_td css_home_text">性别：</td>
                    <td colSpan='2'>
                    <label htmlFor="r5"  className="css_home_text"><input style={{margin: 10}} id="g1" type="radio" value="male" name="gender" defaultChecked/>男</label>  
                    <label htmlFor="r6" style={{margin: 20}}  className="css_home_text"><input style={{margin: 10}} id="g2" type="radio" value="female" name="gender"/>女</label>  
                    </td>
                    </tr>

                    <tr>
                    <td className="css_div_table_td css_home_text" >关系：</td>
                    <td> <input className="css_input "  ref="relationship" id={102} type="text" placeholder="请输入关系" autoFocus={false}/></td>
                    </tr>


                    <tr>
                    <td className="css_div_table_td css_home_text" ><span className="css_div_text_alert"></span>手机：</td>
                    <td> <input className="css_input "  ref="phone" id={103} type="text" placeholder="请输入手机" autoFocus={false}/></td>
                    
                    </tr>

                    <tr>
                    <td className="css_div_table_td " ><span className="css_div_text_alert css_home_text"></span>钱包地址：</td>
                    <td><input className="css_input "  ref="wallet_address" id={104} type="text" placeholder="请输入Nas钱包地址（接收生日红包用）" autoFocus={false}/></td>
                    
                    </tr>

                    <tr>
                    <td className="css_div_table_td css_home_text" >生日/纪念日：
                    </td>
                    <td>
                    <input className="css_input" ref="birthday" type="date" id="bdata"  value="2018-08-05" placeholder="生日/纪念日期"  
                      onChange={()=>{
                        var startDate = document.getElementById ("bdata");
                        console.log("choice = date= "+startDate);

                      }} />
                    
                    </td>
                    
                    </tr>

                
                    {/* <tr>
                    <td colSpan='2'> <span className="css_div_table_td" style={{paddingLeft: 10}}> 注意：带 * 为必填项</span></td>
                    </tr> */}
                    <tr>
                        <td colSpan='2' className="css_div_table_td">
                            <button className="css_home_button css_home_button_add"
                                    onClick={() => {
                                        var categoryId = document.getElementsByName("category");
                                        var category = Utils.getRadioValue(categoryId);
                                        var name = this.refs.name.value;
                                        var genderValue = document.getElementsByName("gender");
                                        var gender = Utils.getRadioValue(genderValue);
                                        var relationship = this.refs.relationship.value;
                                        var phone = this.refs.phone.value;
                                        var walletAddress = this.refs.wallet_address.value;
                                        var birthday = this.refs.birthday.value;
                                        var createTime = Utils.getCurrentTimes();
                                        
                                        
                                        if(name === "")
                                        {
                                            alert("请输入姓名！");
                                            return;
                                        } 
                                        if(relationship === ""){
                                            alert("请输入关系");
                                            return;
                                        }
                                        if(birthday === ""){
                                            alert("请输入生日/纪念日");
                                            return;
                                        }
                                
                                        var jsonData = {"id": "0", "name": name, "gender": gender, "relationship": relationship, "phone": phone, 
                                        "walletAddress": walletAddress, "birthday": birthday, "category": category, "createTime": createTime};
                                        console.log("the input value is ", jsonData);
                                        
                                        if(jsonData === "")
                                        {
                                            alert("请输入完整信息.");
                                        } else {
                                            this.props.submitAddListener(JSON.stringify(jsonData))}
                                        }
                                    }> 提交 </button>
                        </td>
                    
                    </tr>
                </tbody>
                </table>
			</fieldset>
        )
    }

    addItemLayer() {
        return (
            <div id="#addItems">
                <section id="page-breadcrumb">
                    <div class="vertical-center sun">
                        <div class="container">
                            <div class="row">
                                <div class="action">
                                    <div class="col-sm-12">
                                        <h1 class="title css_home_text_title">添加生日</h1>
                                        <p>Add a new record</p>
                                    </div>                        
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
                <section class="container">        
                <div>
                    {this.inputForm()}
                </div>
                </section>
            </div>

        )
    }

    displayDataLayer(){
        return(
            <div name="#displayAllData">
                <section id="page-breadcrumb">
                    <div class="vertical-center sun">
                        <div class="container">
                            <div class="row">
                                <div class="action">
                                    <div class="col-sm-12">
                                        <h2 class="title css_home_text_title">存档生日列表</h2>
                                        <p>Display a list of birthday info</p>
                                    </div>                        
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
                <section class="container">        
                    <div style={{paddingBottom: 30, minHeight: 450}}>
                        {this.props.listDataArrays == "" ? <p className="css_contract_text" style={{marginTop: 100}}>暂无查询数据</p> :
                        <DisplayList data_arrays={this.props.listDataArrays} sendRedPacketListener={this.props.sendRedPacketListener} />
                        }
                    </div>
                </section>
            </div>
        )
    }

    render(){ 
        const{
            listDataArrays,
            submitAddListener,
            queryDataListener,
            sendRedPacketListener,
        }=this.props;

        return (
           <div id="container">
            {this.topLayer()}
            {this.addItemLayer()}
            {this.displayDataLayer()}
            <Footer  />
            </div>
        )
    }
}

export default Home;


